@use "../../Style/import.scss" as import;
.katalog {
	margin-top: 75px;
	&__container {
		@include import.mainContainer();
		margin: none;
	}
	&__content {
		display: flex;
		justify-content: center;
		transition: justify-content 0.5s;
		&.transform {
			justify-content: flex-end;
		}
	}
}
